import {GameImage} from "./GameImage";

type gameid = {
    params : {id: string}
}

export default async function GameDetail({params} : gameid) {
    
    const res = await fetch(`https://api.rawg.io/api/games/${params.id}?key=${process.env.RAWG_API_KEY}`)
    if (!res.ok) {
        throw new Error ('failed')
    } 
    const game = await res.json();

     return (
        <div className="container mx-auto px-4">
            <div className="flex flex-col lg:flex-row gap-8 py-20">
                {/* Main Game Image with Screenshot Overlay */}
                <div className="relative lg:w-1/2">
                    <GameImage 
                        background_image={game.background_image}
                        gameName={game.name}
                        screenshots={game.short_screenshots}
                    />
                </div>

                {/* Game Information */}
                <div className="lg:w-1/2 flex flex-col justify-start">
                    <h1 className="text-4xl font-bold mb-6 text-center lg:text-left">{game.name}</h1>
                    
                    {/* Additional game info */}
                    <div className="space-y-4">
                        {game.released && (
                            <p className="text-lg">
                                <span className="font-semibold">Release Date:</span> {game.released}
                            </p>
                        )}
                        
                        {game.rating && (
                            <p className="text-lg">
                                <span className="font-semibold">Rating:</span> {game.rating}/5
                            </p>
                        )}
                        
                        {game.genres && game.genres.length > 0 && (
                            <p className="text-lg">
                                <span className="font-semibold">Genres:</span> {game.genres.map((genre: any) => genre.name).join(', ')}
                            </p>
                        )}
                        
                        {game.description_raw && (
                            <div className="mt-6">
                                <h2 className="text-xl font-semibold mb-2">Description</h2>
                                <p className="text-gray-700 leading-relaxed">{game.description_raw}</p>
                            </div>
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
}